import Diver from './diver.js';
import SandDollar from './sanddollar.js';
// import Pearl from './pearl.js';
import Shark from './shark.js';

export default class Game {
    constructor() {
    
    this.canvas = document.getElementById('canvas');
    this.ctx = canvas.getContext("2d");
    this.x = canvas.width / 2;
    this.y = canvas.height / 2;
    this.diver = new Diver(); 
    this.sanddollar = new SandDollar();
    this.shark = new Shark(); 
    }

    render() {
        this.sanddollar.render(this.ctx, this.x, this.y)
        this.diver.render(this.ctx, this.x, this.y);
        this.shark.render(this.ctx, this.x, this.y);
        
    }
}