export default class Shark {
    constructor() {
        this.height = 8;
        this.width = 40;
    }

    render(ctx, x, y) {
        
        ctx.fillStyle = "grey"; // body
        ctx.fillRect( 
        x + 40, y - 50,
        this.width, this.height,
        );

        ctx.beginPath(); // head
        ctx.moveTo(170, 25);
        ctx.lineTo(x + this.width, 25);
        ctx.lineTo(x + this.width, 33);
        ctx.fill();

        ctx.beginPath(); // tail
        ctx.moveTo(180 + this.width + 20, 25);
        ctx.lineTo(x + (this.width * 2), 25);
        ctx.lineTo(x + (this.width * 2), 33);
        ctx.fill();

        ctx.beginPath(); // tail 2
        ctx.moveTo(180 + this.width + 15, 25);
        ctx.lineTo(x + (this.width * 2 + 20), 20);
        ctx.lineTo(x + (this.width * 2 + 20), 30);
        ctx.fill();

        ctx.fillStyle = "black" // eye
        ctx.fillRect(x + 34, y-49, 2, 1);

        ctx.fillStyle = "grey"; // mouth
        ctx.beginPath(); // tail 2
        ctx.moveTo(145 + this.width + 15, 25);
        ctx.lineTo(x + (this.width + 20), 20);
        ctx.lineTo(x + (this.width + 20), 30);
        ctx.fill();
    }
}